import React, { useState } from 'react';

import { Link } from 'react-router-dom';
import Flower from '../images/flower-svgrepo-com.svg';
import './Quiz.css';

const questions = [
	{
		id: 1,
		question:
			'Mn n no c hai bn mnh cng thch nht v s n mng ngy 8.3?',
		choices: [
			'Ph nm gu & Cafe sa ',
			'Fine Dinning at Cranes in DC',
			' c ngy khi n',
		],
		correctAnswer: 'Ph nm gu & Cafe sa ',
	},
	{
		id: 2,
		question: 'Mu anh thch nht l mu g?',
		choices: ['Mu nng', 'Mu mt em', 'Mu xanh LinkedIn'],
		correctAnswer: 'Mu mt em',
	},
	{
		id: 3,
		question:
			'Hon thnh cu ht: "V anh ......, v anh p, v anh put it in the bag"',
		choices: ['chn ngn kiu sa', 'hin du nt na gi d bo vng', 'nht'],
		correctAnswer: 'hin du nt na gi d bo vng',
	},
];

//
const Quiz = () => {
	const [currentQuestion, setCurrentQuestion] = useState(0);
	const [endGame, setEndGame] = useState(false);
	const [correct, setCorrect] = useState(true);
	const [congrats, setCongrats] = useState(false);

	const handleAnswerClick = (choice) => {
		if (choice !== questions[currentQuestion].correctAnswer) {
			setCorrect(false);
		}
		if (choice === questions[currentQuestion].correctAnswer) {
			setCorrect(true);
			setCongrats(true);
			setTimeout(() => {
				if (currentQuestion < questions.length - 1) {
					setCurrentQuestion(currentQuestion + 1);
					setCongrats(false);
				} else {
					setEndGame(true);
				}
			}, 1500);
		}
	};

	return (
		<div className='quiz-bg'>
			{endGame ? (
				<div
					className='d-flex flex-column justify-content-center align-items-center 
        result-section'
				>
					<h3>Em b gii qu chc mng em!!</h3>
					<Link to='/quachoem'>
						<button className='custom-button'>Last surprise for nhiuu</button>
					</Link>
				</div>
			) : (
				<div className='question-wrapper container-fluid'>
					<div className='row question-section justify-content-center align-items-center'>
						<div>
							<h3>Cu hi th {currentQuestion + 1}</h3>
							<h3>{questions[currentQuestion].question}</h3>
						</div>
					</div>
					<div className='warning-section'>
						{!correct && <h4>Em ngh thm i gn ng ri c ln!</h4>}
						{congrats && <h4>Paypi gii qu xalangheee</h4>}
					</div>
					<div className='answer-section'>
						{questions[currentQuestion].choices.map((choice, index) => (
							<button
								className='custom-button m-4'
								key={index}
								onClick={() => handleAnswerClick(choice)}
							>
								{choice}
							</button>
						))}
					</div>
					<div className='d-flex justify-content-center align-items-center flower-section'>
						<img src={Flower} alt='flower svg' className='flower-svg' />
					</div>
				</div>
			)}
		</div>
	);
};

export default Quiz;
